import React from 'react';
import './Project.scss';
import projects from '../../assets/projects.json';

const Project = (props) => (
    <section className="project">
            <div dangerouslySetInnerHTML={{__html: projects[props.page].text}}/>
            {projects[props.page].website != "" && <a className='blue' href={projects[props.page].website}>Website</a>}
    </section>
);
 
export default Project;