import React, {useState, useEffect, useCallback} from 'react';
import { InView } from 'react-intersection-observer';
import { isMobile } from 'react-device-detect';
import './Home.scss';
import ProjectList from '../projectList/ProjectList';
import Project from '../project/Project';
import About from '../about/About';

const Home = () => {
  const [page, setPage] = useState("home");
  const [returnHome, setReturnHome] = useState(false);
  const [section, setSection] = useState(<>Here's <span id="blur">some stuff I've done</span></>);

  useEffect(() => {
    var leftSection = document.getElementById('left')
    var rightSection = document.getElementById('right')
    if(leftSection && rightSection && page === "home"){
      if(isMobile || !returnHome){
        leftSection.classList.add('introAnimation')
        rightSection.classList.add('introAnimation')
      }
    }
  }, [])

  const wrappedSetPage = useCallback(x => {
    if(!isMobile){
      var leftSection = document.getElementById('left')
      var rightSection = document.getElementById('right')
      if(leftSection && rightSection){
        leftSection.classList.remove('introAnimation')
        rightSection.classList.remove('introAnimation')
        leftSection.classList.remove('slideInDelayReverse')
        rightSection.classList.remove('slideInReverse')
        leftSection.classList.add('slideOut')
        rightSection.classList.add('slideOutDelay')
      }
      setTimeout(function () {
        if(leftSection && rightSection){
          leftSection.classList.remove('slideOut')
          rightSection.classList.remove('slideOutDelay')
        }
        setPage(x)
      }, 750);
    }
    else{
      setPage(x)
      window.scrollTo(0, 0);
    }
  }, [setPage]);

  const transitionBack = () =>{
    if(!isMobile){
      var backButton = document.getElementById('back')
      var leftSection = document.getElementById('left')
      var rightSection = document.getElementById('right')
      if(leftSection && rightSection && backButton){
        backButton.classList.add('slideOutDelayReverse')
        leftSection && leftSection.classList.add('slideOutDelayReverse')
        rightSection && rightSection.classList.add('slideOutReverse')
      }
      setTimeout(function () {
        if(leftSection && rightSection && backButton){
          backButton.classList.remove('slideOutDelayReverse')
          leftSection.classList.remove('slideOutDelayReverse')
          rightSection.classList.remove('slideOutReverse')
        }
        setReturnHome(true)
        setPage('home')
      }, 750);
    }
    else{
      setPage('home');
      window.scrollTo(0, 0);
    }
  }

  //Left Text
  var title = "Dan Chepkwony";
  var sub = <><span>Hi</span>, I'm</>;
  var description;
  switch(page){
    case 'muncie':
      title = "Muncie Afghan Refugee Resettlement"
      sub = "React Typescript, CSS"
      description = "Collecting online donations for a nonprofit"
      break;
    case 'puzzle':
      title = "Puzzle Wall"
      sub = "Unity, C#"
      description = "A multiplayer TV Board puzzle game"
      break;
    case 'sportslink':
      title ="Sportslink Application Form"
      sub = "React Javascript, Azure Functions, C#"
      description = "Sending application information to Google Sheets"
      break;
    case 'slack':
      title = "Status Board Slack App"
      sub = "Azure Functions, C#, MySQL"
      description = "Collecting online donations for a nonprofit"
      break;
  }
    
  return (
    <main>
    <div className="home">
      {page !== "home" && <button id="back" className={!isMobile ? 'back blue slideIn' : 'back blue'} onClick={transitionBack}>{"< Back"}</button>}
      <div id='left' className={
        page !== 'home' && page !== 'about' && !isMobile  ? 'left slideIn' :
        returnHome == true && !isMobile  ? 'left slideInDelayReverse' : 'left'
      }>
        <h2>{sub}</h2>
        <h1>{title}</h1>
        <p>{page === "home" ? section : description}</p>
        <div className="threeJSContainer">
        </div> 
      </div>
      <div id='right' className={
        page !== 'home' && page !== 'about' && !isMobile  ? 'right slideInDelay' :
        returnHome == true && !isMobile ? 'right slideInReverse' : 'right'
      }>
        {page === "home" && 
        <>
          <ProjectList setPage={wrappedSetPage}/>
          <InView onChange={(inView) => {
            inView && !isMobile ? setSection(<>Here's <span id="blur">more about me</span></>) : setSection(<>Here's <span id="blur">some stuff I've done</span></>)
            var blurElem = document.getElementById('blur');
            blurElem.classList.remove('blur');
            setTimeout(function () {
              blurElem.classList.add('blur');
            }, 50)
            }}>
            <About/>
          </InView>
        </>}
        {page !== "home" && page !== "about" &&
          <Project page={page} />
        }
      </div>
    </div>
    </main>
  )
}
 
export default Home;